@use 'variables' as v;

h1,h2,h3{
    font-family: v.$fuentePrincipal ;
    margin: 0 0 v.$separacion 0;
    text-transform: uppercase;
    text-align: center;
    line-height: 1.5;
}

h1{
    font-size: 3rem;
}

h2{
    font-size: 2.6rem;
    color:v.$primario;
    
    span{
        color: v.$secundario;
        font-size: 1.6rem;
        display: block;
    }

    &::after{
        content: '';
        display: block;
        width: 10rem;
        height: 10rem;
        margin: 0 auto;
        background-image: url("../assets/img/home/cafe.svg");
        background-size: 10rem;
    }
}

.heading-blanco{
    @extend h2;
    color: v.$blanco;
    span{
        color: v.$blanco
    }
    &::after{
        display: none;
    }
}

h3{
    font-size: 2.5rem;
}
